<script>
// import { api } from '@/plugins/http-api'
import { mapState } from 'vuex'
export default {
  props: {
    random: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      rotated: false,
      rotating: false,
      animationDuration: 10000,
      index: 5,
      prizes: [
        {
          value: '7 UP',
          rotation: 0
        },
        {
          value: '20 UP',
          rotation: 270
        },
        {
          value: '50 UP',
          rotation: 180
        },
        {
          value: '100 UP',
          rotation: 90
        },
        {
          value: '10 UP',
          rotation: 135
        },
        {
          value: '30 UP',
          rotation: 45
        },
        {
          value: '70 UP',
          rotation: 315
        },
        {
          value: '150 UP',
          rotation: 225
        }
      ],
      rotation: 1440,
      currentRotation: 0
    }
  },
  computed: {
    ...mapState('user', [
      'free_spin',
      'spin'
    ]),
    spins () {
      return this.free_spin + this.spin
    }
  },
  methods: {
    rotate () {
      if (!this.rotating) {
        // if (this.random) {
        //   this.currentRotation++
        //   this.index = Math.round(Math.random() * 8 - 0.5)
        //   this.rotated = true
        //   this.rotating = true
        // } else {
        //   if (this.spins) {
        //     api.get('profile/wheel')
        //       .then(response => {
        //         console.log('wheel-me', response)
        //         this.currentRotation++
        //         this.index = response.data.data - 1
        //         this.rotated = true
        //         this.rotating = true
        //         setTimeout(async () => {
        //           await this.$store.dispatch('user/init')
        //           this.rotating = false
        //           this.$emit('getPrize', this.prizes[this.index].value)
        //         }, this.animationDuration)
        //       })
        //   }
        // }
        this.currentRotation++
        this.index = Math.round(Math.random() * 8 - 0.5)
        this.rotated = true
        this.rotating = true
        setTimeout(async () => {
          await this.$store.dispatch('user/init')
          this.rotating = false
          this.$emit('getPrize', this.prizes[this.index].value)
        }, this.animationDuration)
      }
    }
  }
}
</script>

