const getters = {
  ballance (state) {
    return state.ballance
  },
  minOrder (state) {
    return state.minOrder
  },
  isPremium (state) {
    return !!state.subscription
  },
  isSubscription (state) {
    return !!state.vip
  },
  isBloger (state) {
    return state.role === 6
  },
  premiumEnds (state) {
    return state.subscription_date
  },
  id (state) {
    return state.id
  },
  fullName (state) {
    return `${state.first_name} ${state.last_name}`
  },
  url (state) {
    return `https://instagram.com/${state.username}`
  }
}

const mutations = {
  UPDATE (state, payload) {
    Object.assign(state, payload)
  },
  UPDATE_LEVEL (state, payload) {
    state.currentLevel = payload.currentLevel
    state.nextLevel = payload.nextLevel
  },
  UPDATE_MIN_SUM (state, payload) {
    state.minOrder = payload
  },
  SET_LOADING_STATUS (state, payload) {
    state.loading = payload
  }
}

